@import url('https://fonts.googleapis.com/css2?family=Frijole&family=Love+Ya+Like+A+Sister&family=Megrim&family=Orelega+One&family=Saira+Stencil+One&family=Sedgwick+Ave+Display&family=Tillana:wght@500&family=Wallpoet&display=swap');

$font-1: 'Wallpoet', cursive;
$font-2: 'Dosis', sans-serif;
$font-3: 'Frijole', cursive;
$font-4: 'Love Ya Like A Sister', cursive;
$font-5: 'Megrim', cursive;
$font-6: 'Orelega One', cursive;
$font-7: 'Saira Stencil One', cursive;
$font-8: 'Sedgwick Ave Display', cursive;
$font-9: 'Tillana', cursive;

$color-1: #4a536b;
$color-2: #e5e5dc;
$color-3: #ff9a8d;
$color-4: #e5e5dc;
$color-5: #ff9a8d;

$white: #ddd;
$black: #333;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$minHeight: 95vh; 

@mixin verticalCenter {
    display: flex;
    jusitfy-content: center;
    align-items: center;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 1rem auto;
    max-width: 1200px;
    font-family: $font-2;
    color: $black;
}

button {
    cursor: pointer;
}

li {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: $black;
    cursor: pointer;
}

a:visited {
    color: $black;
}

.home, .knowledges, .portfolio, .contact {
    display: flex;

    @media screen and (max-width: 850px){
        display: block;
    }
}

.homeContent, .knowledgesContent, .portfolioContent, .contactContent {
    margin-left: .4rem;

    @media screen and (maw-width: 850px) {
        margin-left: 0 !important;
    }
}